@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
}

.app {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

input.error {
  border-color: red;
}


.input-feedback {
  color: red;
  height: 5px;
  margin-top: -12px;
}

@font-face {
    font-family: "NotoSerifKR";
    src: url(/static/media/NotoSerifKR-Regular.22b87ae0.otf);
  }

.landing {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.landing_title{
    color: red;
    font-size: 70px; 
    font-family: 'Oranienbaum', serif;
    font-weight: bold;
}
.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
  color: white; 
}

#btn:hover{
  border-color: red;
}
.menu {
  padding: 0 20px;
  border-bottom: solid 1px white;
}
#btn{
float: right;
}
.menu__logo {
  width: 200px;
  float: left;
}

.menu__logo a {
  display: inline-block;
  font-size: 20px;
  padding: 19px 20px;
  color : red;
  font-family: 'Oranienbaum', serif;
  font-weight: 700;
}

a { text-decoration:none !important }
a:hover { text-decoration:none !important }

.menu__container .ant-menu-item {
  padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
}

.submenu:hover{
color:red !important;
}

.menu__container .ant-menu-item a:hover,
.menu__container .ant-menu-submenu :hover{
  color: rgb(255, 0, 0);
}

.hoverToRed:hover{
  border-bottom: 1px solid red !important;
}
#logout{
  color: white;
}
#logout:hover{
  color: red;
}
.menu__container .menu_left {
  float: left;
}
.menu__container .menu_right {
  float: right;
}


.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
}

@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu {
    display: none;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}
.intro {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.intro_title {
    color: red;
    font-size: 250%;
    border-bottom: solid;
    border-width: 1px;
    border-color: white;
    font-family: 'Oranienbaum', serif;
    font-weight: 700;
}

.intro_body {
    margin-top: 10px;
    text-align: center;
    color: white;
    font-size: 150%;
    font-family: "NotoSerifKR";
}

.intro_button {
    margin-top: 15px;
    background-color: red;
    color: white;
    width: 200px;
    height: 80px;
    font-size: 150%;
    font-family: 'Oranienbaum', serif;
}

.intro_nav{
    float: center;
    background-color: white;
    text-align: center;
    position: fixed;
    top: 45%;
    left: 20px;
    margin: 0px;
}

.intro_nav li{
    border-bottom: solid;
    border-width: 1px;
    border-color: black;
}

.intro_nav li a{
    font-size: 100%;
    font-family: 'Oranienbaum', serif;
    font-weight: 700;
    color: black;
    text-decoration: none;
    padding: 5px;
}
.report{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "NotoSerifKR";
    text-align: center;
}

.report_title {
    color: white;
    font-size: 32px;
    border-bottom: 1px;
    border: solid;
    border-color: white;
}

.report_subtitle {
    padding-top: 20px;
    padding-bottom: 5px;
    color: white;
    font-size: 150%;
}
.credit {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "NotoSerifKR";
}

.credit_title {
    color: white;
    font-size: 250%;
    text-align: center;
    position: relative;
}

.credit_subtitle {
    padding-left: 130px;
    color: gray;
    font-size: 150%;
    text-align: right;
}

.credit_body {
    padding-left: 20px;
    color: white;
    font-size: 150%;
    text-align: left;
}

.credit_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 10px;
}

.credit_body a{
    color: white;
}
